import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'FormSearchCommentEndCourse' })
class FormSearchCommentEndCourse extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      updateQueryParams(values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={5}>
            <Form.Item label="Họ tên SV">{getFieldDecorator('fullName', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Nhận xét">
              {getFieldDecorator('note', {})(<Input onChange={this.props.changeNote} />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchCommentEndCourse;



