import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, Btn } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { TableData } from 'components/common';
import {
  getEstimatedNote,
  updatEstimatedNote,
  downloadFileEstimatedNote,
} from 'services/EstimatedNote';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchCommentEndCourse from './FormSearchCommentEndCourse';

class ListCommentEndCoursePage extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.state = {
      reload: 0,
      listId: [],
      estimatedNote: '',
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  saveDataCommentEndCourse = () => {
    const { listId, estimatedNote } = this.state;
    const dataUpdate = {
      ids: listId,
      estimatedNote,
    };
    if (estimatedNote === '') {
      message.warning('Vui lòng điền nhận xét');
      this.refForm.current.instances.note.focus();
    } else if (!listId.length) {
      message.warning('Vui lòng chọn ít nhất 1 sinh viên');
    } else {
      updatEstimatedNote(dataUpdate).then(() => {
        message.success('Chỉnh sửa thành công');
        this.setState({
          reload: this.state.reload + 1,
        });
      });
    }
  };

  changeEstimatedNote = e => {
    this.setState({
      estimatedNote: e.target.value,
    });
  };

  dowloadFilePdf = () => {
    const { paramSearchClass } = this.state;
    downloadFileEstimatedNote(paramSearchClass);
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'toScores',
      },
      {
        title: 'Nhận xét',
        dataIndex: 'estimatedNote',
        key: 'estimatedNote',
      },
    ];
    const { reload, paramSearchClass } = this.state;
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const listId = [];
        selectedRows.map(record => listId.push(record.studentId));
        this.setState({
          listId,
        });
      },
    };
    return (
      <Card title="Nhận xét cuối khóa">
        <Row>
          <Col span={24}>
            <FormSearchCommentEndCourse
              changeNote={this.changeEstimatedNote}
              ref={this.refForm}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              rowSelection={rowSelection}
              reload={reload}
              formSearch
              service={getEstimatedNote}
              paramSearch={paramSearchClass}
              columns={columns}
              rowKey="studentId"
            />
          </Col>
          <Col span={24} />
          <Col offset={8} span={12}>
            <BtnSave onClick={this.saveDataCommentEndCourse} htmlType="submit" />
            <Btn
              style={{ height: 35, marginLeft: 8, marginRight: 8 }}
              onClick={this.dowloadFilePdf}
              icon="download"
            >
              Download
            </Btn>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListCommentEndCoursePage />
  </Page>
);



