import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getEstimatedNote = dataSearch =>
  request({
    prefix,
    url: `/Student/GetStudentWithEstimatedNote`,
    params: { ...dataSearch },
    method: 'GET',
  });

export const updatEstimatedNote = dataUpdate =>
  request({
    prefix,
    url: `/Student/UpdateEstimatedNote`,
    data: dataUpdate,
    method: 'PUT',
  });

export const downloadFileEstimatedNote = dataUpdate =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: `/Student/ExportStudentWithEstimatedNote`,
    data: dataUpdate,
    method: 'POST',
  });



